

















































































































































































































import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { Table } from "element-ui";
@Component({})
export default class ProTable extends Mixins(TwoWay) {
    /**
     * 表格配置
     */
    @Prop()
    tableOptions!: any;

    /** 需要展示的列 */
    get showTableColumns() {
        const { columns } = this.tableOptions;
        let result: any[] = [];
        if (columns) result = columns.filter((item: any) => item.if !== false);

        return result;
    }
    /**展示的列中需要转化的字典项的列 */
    get dictionaryColumns() {
        const { showTableColumns } = this;
        let result: any[] = [];
        if (showTableColumns.length)
            result = showTableColumns.filter((item: any) => item.dictionary);
        return result;
    }
    /**  最终渲染的数据源 */
    get finalDataSource() {
        const { dictionaryColumns, currentValue } = this;
        let result: any = currentValue;
        // 是否存在字典项
        if (dictionaryColumns.length) {
            const listData = JSON.parse(JSON.stringify(currentValue));
            for (const iterator of dictionaryColumns) {
                const prop = iterator.prop;
                const dictionary = iterator.dictionary;
                result = listData.map((item: any) => {
                    const value = item[prop];
                    let $d_label = "-";
                    let $d_type = "";
                    const findItem = dictionary.find(
                        (ele: any) =>
                            (value || value === 0) && ele.value.toString() === value.toString()
                    );
                    if (findItem) {
                        const { label, type } = findItem;
                        $d_label = label;
                        $d_type = type;
                    }
                    item[prop] = $d_label;
                    item["$d_type"] = $d_type;
                    return item;
                });
            }
        }
        return result;
    }

    getPriview(value: string | string[]) {
        let reslut: string | string[] = [];
        if (typeof value === "string") {
            reslut = value.split(",");
        } else if (typeof value === "object" && value instanceof Array) {
            reslut = value;
        } else {
            console.error("图片展示列数据格式应当 字符串链接 或者 数组链接");
        }
        return reslut;
    }
    /** 处理表格排序变化 */
    handleSortChange(result: any) {
        this.$emit("update:sortData", { prop: result.prop || "", order: result.order || "" });
    }
    /** 处理表格选择变化 */
    handleSelectionChange(result: any) {
        this.$emit("update:selected", result);
    }
    // 切换选中
    toggleAllSelection() {
        (this.$refs["table-hook"] as Table).toggleAllSelection();
    }

    calcBtnGroupChildren(group: any[] = [], index: number, row: any) {
        group = group.filter(item => {
            const result = this.calcBtnGroupColumnButtonIf(row, index, item.if);
            if (result) {
                return item;
            }
        });
        return group;
    }

    calcBtnGroupColumnButtonLabel(row: any, label: Function | string | number) {
        let result = "--";

        if (typeof label === "function") {
            result = label(row);
        } else if (typeof label === "string" || typeof label === "number") {
            result = label.toString();
        }

        return result;
    }

    calcBtnGroupColumnButtonIf(row: any, index: number, rowIf: Function | boolean | undefined) {
        let result = true;
        switch (typeof rowIf) {
            case "function":
                rowIf(row, index) === false && (result = false);
                break;
            case "boolean":
                result = rowIf;
                break;
        }
        return result;
    }
    /** 处理按钮列点击事件 */
    handleBtnColumnButtonClick(row: any, index: number, handler: Function) {
        if (handler && typeof handler === "function") {
            handler(row, index);
        }
    }
}
